import 'ol/ol.css';
import Map from 'ol/Map';
import View from 'ol/View';
import {KML} from 'ol/format';
import {Tile as TileLayer, Vector as VectorLayer} from 'ol/layer';
import OSM from 'ol/source/OSM';
import VectorSource from 'ol/source/Vector';
import xhr from "xhr";
import {click, pointerMove, altKeyOnly} from 'ol/events/condition';
import Select from 'ol/interaction/Select';
import {Fill, Stroke, Circle, Style} from 'ol/style';
import Overlay from 'ol/Overlay';

var vector = new VectorLayer({
  source: new VectorSource({
    url: 'http://localhost:8000/ManchesterWards.kml',
    format: new KML()
  })
});


var container = document.getElementById('popup');
var content = document.getElementById('popup-content');
var closer = document.getElementById('popup-closer');

var overlay = new Overlay({
  element: container,
  autoPan: true,
  autoPanAnimation: {
    duration: 250
  }
});

var map = new Map({
  layers: [
    new TileLayer({source: new OSM()}), 
    vector
  ],
  target: 'map',
  view: new View({
    center: [-248801, 7064389],
    zoom: 11.5
  }),
  overlays: [overlay],
});

var councillors = {};
xhr("http://localhost:8000/Councillors.json", function(error, res, body){councillors=JSON.parse(body)})

var pointerLoc;
map.on('pointermove', function(evt) {
  pointerLoc = evt.coordinate;
});


var selectClick = new Select({
  condition: click,
  style: new Style({
    fill: new Fill({color: 'rgba(0,255,0,0.4)'}), 
    stroke: new Stroke({color: '#FFFF00', width: 1.25})
  })
});
map.addInteraction(selectClick);

selectClick.on("select", function(e){
  var feature = e.selected[0];
  if(!feature) return;

  var wardName = feature.get('name');
  var wardCouncillors = councillors[wardName];
  content.innerHTML = '<p>'+wardName+'</p>';
  content.innerHTML += "<ul>";
  wardCouncillors.forEach(councillor => {
    content.innerHTML += "<li>" + councillor + "</li>";
  });
  content.innerHTML += "</ul>";
  overlay.setPosition(pointerLoc);
});

closer.onclick = function() {
  overlay.setPosition(undefined);
  closer.blur();
  return false;
};
